import React from 'react';
import Container from 'semantic-ui-react/dist/commonjs/elements/Container';
import Header from 'semantic-ui-react/dist/commonjs/elements/Header';
// import Divider from 'semantic-ui-react/dist/commonjs/elements/Divider';
import Responsive from 'semantic-ui-react/dist/commonjs/addons/Responsive';
import Grid from 'semantic-ui-react/dist/commonjs/collections/Grid';
import Portfolio from './Portfolio';

const HelloPage = () => (
  <div>
    <Grid
      verticalAlign="middle"
      columns={1}
      centered
      style={{
        // height: '80vh'
        margin: "8em auto 4em auto"
      }}
    >
      <Grid.Row>
        <Grid.Column>
          <Container text>
            <Header as="h1" textAlign="left" size="huge" style={{fontSize: "4em"}}>
              {' '}
              Hi, I'm Chaojie
            </Header>
            <Responsive minWidth={768}>
              <p style={{fontSize: "1.25em"}}>
                I develop software that enbraces technologies for people.
                I am especially interested in VR/AR, Game AI and etc. Currently, I am working at Apple with edge-cutting technologies.
                You can reach out to me through <a href="https://twitter.com/zaaaaackszz">Twitter</a> and <a href="mailto: me@zcj.io">Email</a>.
              </p>
            </Responsive>
          </Container>
        </Grid.Column>
      </Grid.Row>
    </Grid>
    <Portfolio/>
  </div>
);

export default HelloPage;



