import React, { Component } from 'react';
import Container from 'semantic-ui-react/dist/commonjs/elements/Container';
import Header from 'semantic-ui-react/dist/commonjs/elements/Header';
import Divider from 'semantic-ui-react/dist/commonjs/elements/Divider';
// import { Container, Header, Divider } from 'semantic-ui-react';
import marked from 'marked';
import Navbar from './Navbar';
// import hljs from 'highlight.js';
// import 'highlight.js/styles/arta.css';

class Post extends Component {
  apiString = 'api/article/';

  state = {
    title: '',
    content: '',
    date: ''
  };

  rawMarkup() {
    const rawMarkup = marked(this.state.content, { sanitize: true });
    return { __html: rawMarkup };
  }

  componentDidMount() {
    this.loadPostFromServer(this.apiString + this.props.match.params.id);
  }

  loadPostFromServer(requestTail) {
    const request = new Request('/' + requestTail, {
      method: 'GET',
      headers: new Headers()
    });
    fetch(request)
      .then(function(response) {
        return response.json();
      })
      .then(json => {
        this.setState({
          title: json['title'],
          content: json['content'],
          date: json['date']
        });
      })
      .catch(function(error) {
        window.location.href = '/404';
      });
  }

  render() {
    // hljs.initHighlightingOnLoad();
    return (
      <div>
        <Navbar />
        <Container text>
          <Header as="h1">
            <Header.Content> {this.state.title} </Header.Content>
            <Header.Subheader> {this.state.date} </Header.Subheader>
          </Header>
          <span dangerouslySetInnerHTML={this.rawMarkup()} />
          <Divider hidden />
        </Container>
      </div>
    );
  }
}

export default Post;



