import React, { Component } from 'react';
import Container from 'semantic-ui-react/dist/commonjs/elements/Container';
import List from 'semantic-ui-react/dist/commonjs/elements/List';
import Label from 'semantic-ui-react/dist/commonjs/elements/Label';
import Item from 'semantic-ui-react/dist/commonjs/views/Item';
import Divider from 'semantic-ui-react/dist/commonjs/elements/Divider';
import Header from 'semantic-ui-react/dist/commonjs/elements/Header';
// import Navbar from './Navbar';

class Portfolio extends Component {
  state = {
    items: []
  };

  componentDidMount() {
    this.loadPostsFromServer();
  }

  loadPostsFromServer() {
    fetch('/api/portfolio')
      .then(response => {
        return response.json();
      })
      .then(json => {
        this.setState({ items: json['items'] });
      })
      .catch(error => {
        console.log(error);
      });
  }

  render() {
    return (
      <div>
        {/* <Navbar /> */}
        <Container text>
          <Header as="h1" textAlign="left" size="huge">
            {' '}
            Projects
          </Header>
          <Divider />
          <Item.Group divided>
            {this.state.items.map(item => (
              <Item key={item.name}>
                <Item.Image src={item.videoImg} />
                <Item.Content>
                  <Item.Header>{item.name}</Item.Header>
                  <Item.Meta>
                    <span>
                      Role: {item.role} | Language: {item.lang} | Date:{' '}
                      {item.date}
                    </span>
                  </Item.Meta>
                  <Item.Extra>
                    {item.videoID !== '' && (
                      <Label as="a" href={item.videoID}>
                        Video
                      </Label>
                    )}
                    {item.link !== {} &&
                      Object.keys(item.link).map(key => (
                        <Label as="a" href={item.link[key]}>
                          {key}
                        </Label>
                      ))}
                  </Item.Extra>
                  <Item.Description>
                    <p>{item.description}</p>
                    <List bulleted>
                      {item.jobs.map((job, index) => (
                        <List.Item key={index}>{job}</List.Item>
                      ))}
                    </List>
                  </Item.Description>
                </Item.Content>
              </Item>
            ))}
          </Item.Group>
        </Container>
        <Divider hidden />
      </div>
    );
  }
}
export default Portfolio;



