import React, { Component } from 'react';
// import { Menu } from 'semantic-ui-react';
import Container from 'semantic-ui-react/dist/commonjs/elements/Container';
import Divider from 'semantic-ui-react/dist/commonjs/elements/Divider';
import Menu from 'semantic-ui-react/dist/commonjs/collections/Menu';
import Responsive from 'semantic-ui-react/dist/commonjs/addons/Responsive';
// import { Divider, Container, Responsive } from 'semantic-ui-react';

class Navbar extends Component {
  state = {};

  handleItemClick = (e, { name, href }) => {
    window.location = href;
    this.setState({ activeItem: name });
  };

  render() {
    const { activeItem } = this.state;
    return (
      <div>
        <Menu stackable borderless={true} size="massive">
          <Container>
            <Menu.Item header link={true} href="/">
              Chaojie
            </Menu.Item>
            <Responsive minWidth={768}>
              <Menu.Item
                position="right"
                link={true}
                href="/post"
                name="Blog"
                active={activeItem === 'Blog'}
                onClick={this.handleItemClick}
              />
            </Responsive>
            <Responsive minWidth={768}>
              <Menu.Item
                name="Portfolio"
                link={true}
                href="/portfolio"
                active={activeItem === 'Portfolio'}
                onClick={this.handleItemClick}
              />
            </Responsive>
            <Responsive minWidth={768}>
              <Menu.Item
                name="Resume"
                link={true}
                href="//drive.google.com/file/d/17SsRlQUkFezrd4rqMvy8A-kg9nEMVD7q"
                active={activeItem === 'Resume'}
                target="_blank"
              />
            </Responsive>
          </Container>
        </Menu>
        <Divider hidden />
      </div>
    );
  }
}

export default Navbar;



